import React from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import { Typography } from "@mui/material";
import { BlobServiceClient } from "@azure/storage-blob";
import Input from "@mui/material/Input";


function App() {

  const [insertUrl, setInsertUrl] = React.useState("");
  const [file, setFile] = React.useState<File | null>(null);

  const BASE_URL = "https://warehouse5containerapp.ambitiouspond-f83f10bb.westus2.azurecontainerapps.io/api/HttpTrigger";

  const submitHandler = (event:any) => {
    event.preventDefault();
    console.log(insertUrl);
    insertData(insertUrl);
    // debugger;
  }

  const insertData = async (data:any) => {
    // post data to api
    await fetch(BASE_URL, {
      method: 'post',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        url: data
      })
    });
    console.log("inserted   "+ data);
  }

  const chooseFile = async (event:any) => {
    event.preventDefault();
    setFile(event.target.files[0]);
    
    console.log(event.target.files[0]);
  };


  const submitFile = async () => {
    if (!file) {
      alert("No file chosen! can't upload.");
    }else{
    console.log("uploading file: ", file)

    const connectionString = "BlobEndpoint=https://storageque5.blob.core.windows.net/;QueueEndpoint=https://storageque5.queue.core.windows.net/;FileEndpoint=https://storageque5.file.core.windows.net/;TableEndpoint=https://storageque5.table.core.windows.net/;SharedAccessSignature=sv=2021-12-02&ss=bfqt&srt=co&sp=rwdlacupiytfx&se=2023-05-05T10:18:32Z&st=2023-04-13T02:18:32Z&spr=https&sig=w9pcjo43hYWcmu68tjDRQLKenk6qdiUEHvbJCtrN2BU%3D";
    const blobServiceClient = BlobServiceClient.fromConnectionString(connectionString);
    const containerName = "foo";
    const containerClient = blobServiceClient.getContainerClient(containerName);

    const blob = new Blob([file], { type: file.type });
    console.log(blob)
    const blockBlobClient = containerClient.getBlockBlobClient(file.name);
    await blockBlobClient.uploadData(blob);
    insertData("https://storageque5.blob.core.windows.net/foo/" + file.name);
    }
  };


  return (
    <div style={{ width: "100%" }}>
    <Box
      component="form"
      sx={{
        '& > :not(style)': { m: 1, width: '25ch' },
      }}
      noValidate
      autoComplete="off"
      onSubmit={submitHandler}
    >
      <TextField id="inputId" label="URL" placeholder="Input a url" variant="standard"
      onChange={e => setInsertUrl(e.target.value)} />
      <Button variant="contained" type="submit">URL Check</Button>
    </Box>

    
          <Box sx={{ display: "flex", flexDirection: "row", gap: "1rem" }}>
            <Input
              type="file"
              onChange={chooseFile}
              sx={{ display: "none" }}
              id="choose-image-input"
            />
            <label htmlFor="choose-image-input">
              <Button variant="contained" component="span">
                Choose Image
              </Button>
            </label>
            <Typography>
              {file && file.name}
            </Typography>

            <Button variant="contained" onClick={submitFile}>
              Image Check
            </Button>

          </Box>


          <Typography variant="h6" color="error.main">
            {insertUrl}
          </Typography>
    </div>
  );
}

export default App;
